.cat-card {
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.cat-card:hover {
    transform: scale(1.1);
}

.cat-card img {
    padding: 10px;
    max-height: 300px;
    margin: auto;
}

.cat-card p {
    font-weight: 600;
    font-size: 1.2rem;
    padding: 5px;
}